import React, { useEffect, useMemo } from 'react'
import { useFormikContext } from 'formik';
import { graphql, navigate, useStaticQuery, withPrefix } from 'gatsby';
import { max } from 'lodash';
import { StaticImage } from 'gatsby-plugin-image';
import { useClipboard, useToast  } from "@chakra-ui/react"

import Box from '../components/Box';
import Flex from '../components/Flex';
import Text from '../components/Text';
import Button from '../components/Button';
import Container from '../components/Container';
import theme, { Media, responsive } from '../components/ThemeProvider/theme';

import Layout from '../containers/Layout'
import resultData from '../json/result.json'

const cityName = {
  france: '法國尼斯',
  germany: '德國慕尼黑',
  hawaii: '美國夏威夷',
  iceland: '冰島雷克雅維克',
  netherlands: '荷蘭阿姆斯特丹',
  "new-york": '美國紐約',
  russia: '俄羅斯莫斯科 ',
  spain: '西班牙伊比薩島',
  sweden: '瑞典斯德哥爾摩',
  turkey: '土耳其伊斯坦堡',
}

const resultInfos = [
  {
    title: '粉絲留言拿獎勵',
    desc: '下載測驗結果圖,到 GoShare 臉書官方粉絲團指定貼文留言分享,就有額外獎勵!',
    linkText: '前往 GoShare 臉書粉專',
    href: '',
  },
  {
    title: '地圖尋找小驚喜',
    desc: '現在到 GoShare App 地圖，',
    city: '前往　',
    cityColor: 'custom.blue',
    text: '尋找你的棒棒糖小驚喜！',
    icon: <StaticImage />,
    linkText: '開啟 GoShare app',
    href: '',
  },
  {
    title: '騎乘抽夢幻機票',
    desc: '9/10 前騎乘 GoShare，憑發票就有機會抽',
    city: ' ',
    cityColor: 'custom.pink',
    cityFontSize: '1.625rem',
    textAlign: 'center',
    px: '1em',
    text: '雙人來回機票！',
  },
]


const Result = ({ pageContext: { id, questions, data } }) => {
  const { values } = useFormikContext()
  const answerNotMatch = useMemo(() => values.answers.length < questions.length, [questions.length, values.answers.length])
  useEffect(() => {
    if (answerNotMatch) {
      navigate(`/q/${values.answers.length + 1}`)
    }
  }, [answerNotMatch])
  const percent = useMemo(() => {
    const p = (values.points * 100) / data[id].reduce((res, scores) => {
      res = res + max(scores)
      return res
    }, 0)
    return Math.round(p)
  }, [data, id, values.points])

  const { site: { siteMetadata: { url } } } = useStaticQuery(graphql`
    query resultQuery  {
      site {
        siteMetadata {
          url
        }
      }
    }
  `)
  const { onCopy } = useClipboard(url)
  const toast = useToast()
  if (answerNotMatch) return null
  const res = resultData[id]
  console.log(resultData)
  return (
    <Layout headerColor="white" footerProps={{ bg: 'custom.blue', pb: '3.5em' }} noPt title={id}>
      <Box pt={theme.headerHeight} bg={res.bg}>
        <Container>
          <Flex>
            <Box as={Media} greaterThanOrEqual="tablet" height="auto" bg={res.bg}>
              <StaticImage src="result_banner.png" layout="fullWidth" />
            </Box>
            <Box textAlign="center" mt={responsive('1em', '0')}>
              <Text fontWeight="bold" color="custom.darkBlue" textAlign="center" fontSize={responsive('1.75rem', '2.625rem')}>
                你是
                <Text.Inline fontSize={responsive('2.25rem', '3.375rem')}> {Math.floor(percent)}% </Text.Inline>
                的
              </Text>
              <Text fontWeight="bold" textAlign="center" color="custom.darkBlue" fontSize={responsive('2.625rem', '4rem')}>{res.personality}</Text>
              <Box as={Media} lessThan="tablet" height="auto" bg={res.bg}>
                <StaticImage src="result_banner.png" layout="fullWidth" />
              </Box>
              <Text fontWeight="bold" fontSize={responsive('1.25rem', '1.5rem')} color="custom.bgColor">最適合去 → <Text.Inline color="custom.darkBlue">{res.city}</Text.Inline></Text>
              <Box borderRadius="1.5rem" p={responsive('2.25rem', '3.25rem')} bg={res.boardBg} color="white" mt="2em">
                <Box pb={responsive('1.25rem', '2rem')} mb={responsive('1.125rem', '1.875rem')} borderBottom="1px solid">
                  <Text fontWeight="bold" whiteSpace="pre-wrap" fontSize={responsive('1em', '1.5em')}>{res.slogan}</Text>
                </Box>
                <Text textAlign="justify" fontSize={responsive('0.75rem', '1rem')}>{res.desc}</Text>
                <Box textAlign="center" borderRadius="1.25rem" my="1.375rem" bg="white" p="0.875rem" fontSize="1.125rem">
                  <Box color="custom.darkBlue" borderBottom="1px solid" borderColor="borderGray" mb="0.25rem">
                    該避免的雷旅伴　
                    <Text.Inline color="custom.blue" fontSize="1.375rem">旅遊精算師</Text.Inline>
                  </Box>
                  <Text fontSize="1rem" color="custom.pink">什麼事都想提前預知，是在當國師？</Text>
                </Box>
              </Box>
            </Box>
          </Flex>
          <Flex mt={responsive('4rem', '3.75rem')} pb="1.875rem" justifyContent="center" flexDirection={responsive('column', 'row')}>
            <Box mx="0.375rem" width="100%">
              <Button.Pink as="a" width="100%" href={withPrefix(`/destination/${id}-${percent}.jpg`)} download={`Goshare-OMG.jpg`}>下載結果圖</Button.Pink>
            </Box>
            <Box mx="0.375rem" mt={responsive('0.75rem', '0')} width="100%">
              <Button.Pink
              onClick={() => {
                onCopy()
                toast({
                  title: "複製成功",
                  status: "success",
                  duration: 3000,
                  isClosable: true,
                })
              }}
              width="100%"
              >複製測驗連結</Button.Pink>
            </Box>
          </Flex>
        </Container>
      </Box>
      <Box bg="custom.blue" py="1.875rem" px="2em">
        {resultInfos.map((d, i) => (
          <Box.Relative
            borderRadius="1.5rem"
            bg="white"
            py="1.875em"
            px={responsive(d.px || '2em', '2.5em')}
            mt={i && "3.375rem"}
          >
            <Flex alignItems="center" justifyContent="center">
              <Text mt="0.5rem" fontSize="3.375rem" lineHeight="1" color="custom.teal">{i + 1}</Text>
              <Box
                ml="0.25em"
                fontSize="1.625rem"
                color="custom.blue"
                borderBottom="1.5px solid"
                borderColor="custom.teal"
              >{d.title}</Box>
            </Flex>
            <Flex>
              <Box mt="1.25em" color="custom.darkBlue">
                <Text fontSize="0.9375em">{d.desc}</Text>
                {d.city && (
                  <Text textAlign={d.textAlign} my="0.5rem">
                    {d.city}
                    <Text.Inline color={d.cityColor} fontSize={d.cityFontSize}>
                      {cityName[id]}
                    </Text.Inline>
                  </Text>
                )}
                <Text textAlign={d.textAlign}>{d.text}</Text>
              </Box>
              {d.icon}
            </Flex>
            {d.linkText && (
              <Box.Absolute width="78%" bottom="0" left="50%" transform="translate(-50%, 50%)">
                <Button.Pink width="100%" href={d.href}>{d.linkText}</Button.Pink>
              </Box.Absolute>
            )}
          </Box.Relative>
        ))}
      </Box>
    </Layout>
  )
}

export default Result
